const main = () => {
  const restaurantList = document.getElementById("restaurant-list"); // Ambil elemen <ul> dari DOM
  const bodyElement = document.querySelector("body");

  fetch("data/DATA.json")
    .then((response) => {
      return response.json();
    })
    .then((responseJson) => {
      const restaurants = responseJson.restaurants;

      // Iterasi melalui setiap restoran dan tambahkan mereka ke dalam daftar
      restaurants.forEach((restaurant) => {
        const listItem = document.createElement("div");
        listItem.classList.add("restoList");
        listItem.innerHTML = `
            <img class="fan-art-club" src="${restaurant.pictureId}" alt="Ini adalah foto dari restoran ${restaurant.name}">
            <h2>${restaurant.name}</h2>
            <h4>From: ${restaurant.city}</h4>
            <p>${restaurant.description}</p>
        `;
        restaurantList.appendChild(listItem);
      });
      // Hitung jumlah elemen yang telah ditambahkan
      const itemCount = restaurants.length;

      // Atur jumlah kolom grid sesuai dengan jumlah elemen
      restaurantList.style.gridTemplateColumns = `repeat(${itemCount}, 1fr)`;
    })
    .catch((error) => {
      console.log(error);
    });
};

export default main;
