ite;\n  background-color:#176b87;\n}\n\n#drawer2.open {\n  transform: translate(0,0)\n}\n\n#drawer2 {\n  display: none;\n  margin: 0;\n}\n\n#drawer2 ul {\n  list-style-type: none;\n  margin: 0;\n  padding: 0;\n  width: 100%;\n}\n\n#drawer2 ul li {\n  display: inline;\n  width: 100%;\n  font-size: large;\n}\n\n#drawer2 a {\n  color: white;\n  text-decoration: none;\n  transition: background-color 0.3s ease;\n  min-width: 44px;\n  min-height: 44px;\n  font-size: large;\n}\n/* Hero Section */\n.hero {\n  display: flex;\n  align-items: center;\n  flex-direction: column;\n  color: #fff;\n  width: 100%;\n  text-align: center;\n  padding: 20px;\n}\n\n.hero_title {\n  color: #fff;\n  font-weight: 300;\n  font-size: 24px;\n  text-shadow: 3px 3px 5px rgba(0, 0, 0, 0.5);\n  margin-bottom: 20px;\n}\n\n.hero_element {\n  display: flex;\n  flex-direction: column;\n  font-weight: lighter;\n  max-width: 400px;\n  width: 100%;\n}\n\n.searchResto {\n  width: 100%;\n  padding: 10px;\n  margin-top: 5px;\n  border-radius: 15px;\n  border: 0;\n  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);\n}\n\n.searchButton {\n  padding: 10px;\n  border-radius: 15px;\n  border: 0;\n  margin-top: 5px;\n  cursor: pointer;\n  box-sizing: border-box;\n  background-color: #176b87;\n  color: white;\n}\n\n.searchButton:hover {\n  background-color: white;\n  color: #176b87;\n}\n\n/* Main Section */\n.result {\n  display: flex;\n  justify-content: center;\n  color: white;\n  margin: 20px;\n}\n\n#restaurant-list{\n  display: flex;\n  flex-wrap: wrap;\n  height: 100%;\n  width: 100%;\n}\n\n.restoList {\n    display: flex;\n    grid-column: 3;\n    flex-direction: column;\n    padding: 10px;\n    width: 100%;\n    max-width: 100%;\n    margin: 20px;\n    margin-top: 0;\n    box-shadow: 0px 5px 6px rgba(0, 0, 0, 0.1);\n}\n\n/* Gaya default untuk perangkat seluler */\n.fan-art-club {\n  max-width: 100%;\n  height: auto; /* Ini akan menjaga aspek rasio gambar */\n  box-sizing: border-box;\n  word-wrap: break-word;\n  overflow: auto;\n}\n\nfooter{\n  background-color: #176b87;\n  padding: 16px;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  position: relative ;\n  width: 100%; /* Menyebarkan footer di seluruh lebar layar */\n}\n\n.footer {\n  font-size: 12px;\n  color: white;\n}\n\n/* Mobile First Approach */\n@media screen and (max-width: 480px) {\n  header {\n    min-height: 350px;\n  }\n  \n  .hero_title {\n    font-size: 20px;\n  }\n  \n  .searchResto {\n    padding: 8px;\n    margin-top: 10px;\n      min-width: 44px;\n  min-height: 44px;\n  }\n  \n  .searchButton {\n    padding: 8px;\n    margin-top: 10px;\n      min-width: 44px;\n  min-height: 44px;\n  }\n\n  .navigasi_atas {\n    display: grid;\n    grid-template-columns: 200px 100px;\n    max-width: 100%;\n    width: 100%;\n  }\n\n  .restoList {\n    width: 100%;\n    max-width: 100%;\n    /* Atur margin atau padding lain sesuai kebutuhan Anda */\n  }\n  \n  /* Menambahkan properti overflow-x untuk menghilangkan pergerakan horizontal */\n  #restaurant-list {\n    display: flex;\n    flex-wrap: wrap;\n    max-width: 100%;\n    padding: 20px;\n  }\n\n  #hamburger {\n    display: flex;\n    justify-content: flex-end;\n  }\n\n  #drawer {\n    display: none;\n  }\n\n  #drawer2{\n    width: 70%;\n    height: 100%;\n    display: flex;\n    position: fixed;\n    z-index: 2;\n    /* menyembunyikan #drawer -250px (sesuai dengan ukuran lebar drawer) ke kiri */\n    transform: translate(-800px, 0);\n    transition: transform 0.3s ease-in-out;\n    background-color:#111;\n  }\n  \n  #drawer2 a {\n    min-width: 44px;\n    min-height: 44px;\n    width: 100%;\n    height: 100%;\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    font-size: large;\n    color: white;\n  }\n\n  #drawer2 ul {\n    display: grid;\n    grid-template-rows: 20px 20px 20px;\n    gap: 60px;\n    overflow: auto;\n    color:#176b87;\n    margin-top: 20px;\n  }\n\n  #drawer2 li {\n    display: flex;\n    min-width: 44px;\n    min-height: 44px;\n    border-radius: 20px;\n    background-color:#176b87;\n  }\n}\n\n/* konfigurasi untuk media baru */\n@media screen and (max-width: 768px) {\n  header {\n    min-height: 350px;\n  }\n  \n  .hero_title {\n    font-size: 20px;\n  }\n  \n  .searchResto {\n    padding: 8px;\n    margin-top: 10px;\n      min-width: 44px;\n  min-height: 44px;\n  }\n  \n  .searchButton {\n    padding: 8px;\n    margin-top: 10px;\n      min-width: 44px;\n  min-height: 44px;\n  }\n\n  .navigasi_atas {\n    display: grid;\n    grid-template-columns: 200px 100px;\n    max-width: 100%;\n    width: 100%;\n  }\n\n  .restoList {\n    width: 100%;\n    max-width: 100%;\n    /* Atur margin atau padding lain sesuai kebutuhan Anda */\n  }\n  #restaurant-list{\n    display: flex;\n    flex-wrap: wrap;\n    max-width: 100%;\n    padding: 20px;\n  }\n\n  #hamburger {\n    display: flex;\n    justify-content: flex-end;\n    left: 400px;\n  }\n\n  #drawer {\n    display: none;\n  }\n  \n  #drawer2{\n    width: 70%;\n    height: 100%;\n    display: flex;\n    position: fixed;\n    z-index: 2;\n    /* menyembunyikan #drawer -250px (sesuai dengan ukuran lebar drawer) ke kiri */\n    transform: translate(-800px, 0);\n    transition: transform 0.3s ease-in-out;\n    background-color:#111;\n    }\n\n  #drawer2 a {\n    min-width: 44px;\n    min-height: 44px;\n    width: 100%;\n    height: 100%;\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    font-size: large;\n    color: white;\n  }\n\n  #drawer2 ul {\n    display: grid;\n    grid-template-rows: 20px 20px 20px;\n    gap: 60px;\n    overflow: auto;\n    color:#176b87;\n    margin-top: 20px;\n  }\n\n  #drawer2 li {\n    display: flex;\n    min-width: 44px;\n    min-height: 44px;\n    border-radius: 20px;\n    background-color:#176b87;\n  }\n\n}\n\n/* Media Queries for Larger Screens (Tetapkan sesuai kebutuhan Anda) */\n@media screen and (min-width: 1024px) {\n  header {\n    min-height: 550px;\n  }\n  \n  .hero_title {\n    font-size: 32px;\n  }\n  \n  .searchResto {\n    padding: 12px;\n    margin-top: 15px;\n    \n  }\n  \n  .searchButton {\n    padding: 12px;\n    margin-top: 15px;\n  }\n\n    #drawer {\n    position: relative;\n    transform: translate(0, 0);\n  }\n \n  #hamburger {\n    display: none;\n  }\n}"],"sourceRoot":""}]);
